import { useState } from 'react';
import { fetchMovies } from './API';
import {
  normalMovies,
  normalMovie,
  normalActors,
  normalReview,
} from './Normalisation';
import toast from 'react-hot-toast';

const useFetch = () => {
  //     ,     
  //    
  const [movies, setMovies] = useState([]);
  //        .
  const [isLoading, setIsLoading] = useState(false);
  // '   ,    
  const [movie, setMovie] = useState(null);
  // ,     
  const [cast, setCast] = useState([]);
  //   
  const [reviews, setReviews] = useState([]);

  //   
  const fetchMoviesList = params => {
    setIsLoading(true);
    fetchMovies(params)
      .then(response => {
        //      
        if (!response.results.length) {
          setIsLoading(false);
          toast.error('Nothing found!');
        }
        const movies = normalMovies(response.results);

        setMovies(movies);
      })
      .catch(error => {
        if (error.message !== 'canceled') {
          setIsLoading(false);
          toast.error(error.message);
        }
      });
  };

  //     .
  const fetchMovie = params => {
    setIsLoading(true);
    fetchMovies(params)
      .then(response => {
        setMovie(normalMovie(response));
      })
      .catch(error => {
        if (error.message !== 'canceled') {
          setIsLoading(false);
          toast.error(error.message);
        }
      });
  };

  // ,    
  const fetchCast = params => {
    setIsLoading(true);
    fetchMovies(params)
      .then(response => {
        setCast(normalActors(response.cast));
        if (!response.cast.length)
          toast.error("We don't have a cast for this film");
      })
      .catch(error => {
        if (error.message !== 'canceled') toast.error(error.message);
      });
  };
  // v
  const fetcReviews = params => {
    fetchMovies(params)
      .then(response => {
        setReviews(normalReview(response.results));
        if (!response.results.length) toast.error('No reviews for this film');
      })
      .catch(error => {
        if (error.message !== 'canceled') toast.error(error.message);
      });
  };

  return {
    movies,
    fetchMoviesList,
    movie,
    fetchMovie,
    cast,
    fetchCast,
    reviews,
    fetcReviews,
    isLoading,
    setIsLoading,
  };
};

export default useFetch;
